export default {
	data() {
		return {
			base: {
				avatar: this.$config.avatar,
				CustomBar: this.CustomBar,
				title: this.$config.title
			},
			moreData: {
				show: false,
				type: 'cosmos',
				id: 0,
			},
			newMessageList: [],
			messageCount: 0,
		};
	},
	created() {
		// 注册打开监听
		
		this.reMessgaeList();
		
		uni.$on('reMessgaeList', () => {
			this.reMessgaeList();
		});
	},
	onLoad() {
		console.log('listchatonLoad')
	},
	methods: {
		// 打开对话
		openChat(index) {
			let user = this.newMessageList[index].user;
			console.log(user);
			this.$db.set('chat',user)
			uni.navigateTo({
				url:'/pages/chat/index',
			})
			this.$common.readNewMessageList(user.user_id);
		},
		// 刷新消息列表
		reMessgaeList() {
			let count = 0;
			this.newMessageList = this.$common.getNewMessageList();
			this.newMessageList.forEach((ele, index) => {
				count += ele.tips.count;
				if (count > 99) {
					this.messageCount = count;
					return;
				}
			});
			this.messageCount = count;
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		}
	}
};

